import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { S1SettlementOrExpirationPeriod } from 'app/core/models/s1-settlement-or-expiration-period.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-s1-incomplete-decision-expiration-period',
  templateUrl: './s1-incomplete-decision-expiration-period.component.html',
  styleUrls: ['./s1-incomplete-decision-expiration-period.component.scss']
})
export class S1IncompleteDecisionExpirationPeriodComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<S1SettlementOrExpirationPeriod>;
  displayedColumns: string[] = ['noOfWorkingDays'];

  s1IncompleteDecisionExpirationPeriod = new S1SettlementOrExpirationPeriod();
  s1IncompleteDecisionExpirationPeriodList = [];

  s1IncompleteDecisionExpirationPeriodForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.s1IncompleteDecisionExpirationPeriod).subscribe((res: any) => {
      this.s1IncompleteDecisionExpirationPeriodList = res.responseData.data;
      this.dataSource = new MatTableDataSource<S1SettlementOrExpirationPeriod>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.s1IncompleteDecisionExpirationPeriod, this.s1IncompleteDecisionExpirationPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        if (!Number(this.s1IncompleteDecisionExpirationPeriod.noOfWorkingDays)) {
          err.error.message = 'Please enter numeric values only';
        } else {
          err.error.message = 'Default Number of working days have already been set. Select the entry to update the record';
        }
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getS1IncompleteDecisionExpirationPeriod(): void {
    this.apiService.findById(ENDPOINTS.s1IncompleteDecisionExpirationPeriod, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.s1IncompleteDecisionExpirationPeriod = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.s1IncompleteDecisionExpirationPeriod, this.s1IncompleteDecisionExpirationPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.s1IncompleteDecisionExpirationPeriodForm = this.formBuilder.group({
      noOfWorkingDays: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.s1IncompleteDecisionExpirationPeriod = new S1SettlementOrExpirationPeriod();
    this.s1IncompleteDecisionExpirationPeriodForm.markAsUntouched();
    this.s1IncompleteDecisionExpirationPeriodForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this announcement?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.s1IncompleteDecisionExpirationPeriod, this.s1IncompleteDecisionExpirationPeriod.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('S1 incomplete decision expiration period successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
